import tColorPicke from '@/components/t-color-picker/t-color-picker.vue';
var _self;
export default {
	components: {
		't-color-picker': tColorPicke
	},
	data() {
		return {
			color: {
				r: 255,
				g: 0,
				b: 0,
				a: 0.6
			},
			isEdit: false,
			fontColor: '#000',
			formats: {},
			readOnly: false,
			placeholder: '描述下你的旅行心得,帮助更多旅行的人...',
			editorHeight: 300,
			keyboardHeight: 0,
			isIOS: false
		};
	},
	onLoad() {
		_self = this;
	},

	methods: {
		cancel() {
			this.isEdit = false;
		},
		open() {
			this.$refs.colorPicker.open();
			this.isEdit = true;
			// uni.hideKeyboard();
		},
		hideKey() {
			uni.hideKeyboard();
		},
		async confirm(e) {
			this.isEdit = false;
			this.fontColor = await e.hex;
			this.onStatusChange({
				detail: {
					color: e.hex
				}
			});
			this.$forceUpdate();
		},
		readOnlyChange() {
			this.readOnly = !this.readOnly
		},
		onEditorReady() {
			uni.createSelectorQuery().select('#editor').context(function(res) {
				_self.editorCtx = res.context;
			}).exec();
		},
		undo() {
			this.editorCtx.undo();
		},

		redo() {
			this.editorCtx.redo();
		},

		blur() {
			this.editorCtx.blur();
		},

		format(e) {
			// this.hideKey();
			let {
				name,
				value
			} = e.target.dataset;
			if (!name) return; // console.log('format', name, value)
			this.editorCtx.format(name, value);
		},

		onStatusChange(e) {
			this.formats = e.detail;
		},

		insertDivider() {
			this.editorCtx.insertDivider({
				success: function() {
					console.log('insert divider success');
				}
			});
		},

		store(e) {
			this.editorCtx.getContents({
				success: function(res) {
					e.currentTarget.id == 1 ? console.log('保存内容:', res.html) : uni.navigateTo({
						url: `../preview/preview?rich=${encodeURIComponent(res.html)}`
					});
				}
			});
		},

		clear() {
			this.editorCtx.clear({
				success: function(res) {
					console.log("clear success");
				}
			});
		},

		removeFormat() {
			this.editorCtx.removeFormat();
		},

		insertDate() {
			const date = new Date();
			const formatDate = `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`;
			this.editorCtx.insertText({
				text: formatDate
			});
		},

		insertImage() {
			// const that = this;
			uni.chooseImage({
				count: 1,
				success: function(res) {
					_self.editorCtx.insertImage({
						src: res.tempFilePaths[0],
						data: {
							id: 'abcd',
							role: 'god'
						},
						width: '80%',
						success: function() {
							console.log('insert image success');
						}
					});
				}
			});
		}
	}
};
